.input {
    height: 75px;
    border-radius: 50px;
    margin-bottom: 20px;
    display: flex;
    justify-content: flex-end; /* Alinear el texto a la derecha*/
    align-items: center;
    font-weight: bold;
    font-size: 30px;
    background-color: #0a0a23;
    color: white;
    padding: 11px 30px 11px 11px; /*superior derecho inferior izquierdo*/
    margin: 10px;
    box-shadow: -1px 4px 1px 1px white;
}